import React from "react";
import DTimage from "../../img/DT.png";
import Media from "react-media";
import "../../style/css/dt.css";

const Dt = () => {
	return (
		<>
			<Media query="(min-width: 992px)">
				{(matches) => {
					return matches ? (
						<section className="mgr-Dagmara-Toporowska-container" id="section3">
							<div className="DT-container">
								<div className="DT-img">
									<img src={DTimage} className="DTimage" />
								</div>
								<div className="DT-description">
									<h1>JAK TO SI ZACZO?</h1>
									<p>
										Dziaalnoci zwizan z tumaczeniami zajmujemy si od roku
										2001. Pocztkowo swoj ofert kierowalimy do osb, ktre
										zainteresowane byy wykonaniem tumacze z jzyka
										angielskiego.
									</p>
									<p>
										Dynamiczny rozwj naszych usug oraz ch wyjcia naprzeciw
										oczekiwaniom kontrahentw sprawiy, e w roku 2005
										zaoylimy Biuro Tumacze. Doskonale wiemy jak pomc
										naszym Klientom, poniewa sami rwnie dokonujemy przekadu.
										Wacicielk Biura jest tumacz przysigy jzyka
										angielskiego mgr Dagmara Toporowska.
									</p>
									<p>
										Dwudziesty pierwszy rok zbierania dowiadcze, tysice
										zrealizowanych zlece i projektw gwarantuje naszym Klientom
										w peni profesjonalne tumaczenia przysige i zwyke.
									</p>
									<button className="see-offer2">Zobacz ofert</button>
								</div>
							</div>
						</section>
					) : (
						<section className="mgr-Dagmara-Toporowska-container" id="section3">
							<div className="DT-container">
								<div className="DT-description">
									<h1>JAK TO SI ZACZO?</h1>
									<p>
										Dziaalnoci zwizan z tumaczeniami zajmujemy si od roku
										2001. Pocztkowo swoj ofert kierowalimy do osb, ktre
										zainteresowane byy wykonaniem tumacze z jzyka
										angielskiego.
									</p>
									<p>
										Dynamiczny rozwj naszych usug oraz ch wyjcia naprzeciw
										oczekiwaniom kontrahentw sprawiy, e w roku 2005
										zaoylimy Biuro Tumacze. Doskonale wiemy jak pomc
										naszym Klientom, poniewa sami rwnie dokonujemy przekadu.
										Wacicielk Biura jest tumacz przysigy jzyka
										angielskiego mgr Dagmara Toporowska.
									</p>
									<p>
										Dwudziesty pierwszy rok zbierania dowiadcze, tysice
										zrealizowanych zlece i projektw gwarantuje naszym Klientom
										w peni profesjonalne tumaczenia przysige i zwyke.
									</p>
									<div className="button-and-DT-img">
										<button className="see-offer2">Zobacz ofert</button>
										<div className="DT-img">
											<img src={DTimage} className="DTimage" />
										</div>
									</div>
								</div>
							</div>
						</section>
					);
				}}
			</Media>
		</>
	);
};

export default Dt;
